h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}
/*  colormind.io */
*,
*::after,
*::before {
  /* this makes the box not include extra size when adding "padding" */
  box-sizing: border-box;
}

:root {
  --page-padding: 1.2rem 10rem;
  --gap: 1rem;


  /* COLORS */
  --light-shades: #f3f3f4;
  --main-brand: #724296;
  --dark-accent: #333;
  --dark-shades: #232824;
}
.navbar-container {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}
@media screen and (max-width: 768px) {
  :root {
    --page-padding: 1rem 2rem;
  }
  .navbar-container {
    flex-direction: column;
    gap: var(--gap);
  }

  .navbar-links-container {
    flex-direction: column;
  }
}