.portfolio-container {
  padding: var(--page-padding);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--gap);
  margin-top: 10%;
}

.profolio-projects-frame img {
  width: 300px;
  height: 200px;
  object-fit: cover;
  border-radius: var(--border-radius);
  cursor: pointer;
}

.profolio-projects-frame img:hover {
  filter: brightness(0.8);
}

.portfolio-grid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--gap);
  justify-items: center;
}
